import "./Header.css";
import React, { useState } from "react";
import { SpeedDial } from "@material-ui/lab";
// import { user } from "./Navbar";
import Backdrop from "@material-ui/core/Backdrop";
import DashboardIcon from "@material-ui/icons/Dashboard";
import PersonIcon from "@material-ui/icons/Person";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import ListAltIcon from "@material-ui/icons/ListAlt";
import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";
import { useHistory } from "react-router-dom";
import { useAlert } from "react-alert";
import { logout } from "../../../actions/userAction";
import { useDispatch, useSelector } from "react-redux";
//  frontend/src/actions/userAction
const UserOptions = ({ user }) => {
  const { cartItems } = useSelector((state) => state.cart);

  const [open, setOpen] = useState(false);
  const history = useHistory();
  const alert = useAlert();
  const dispatch = useDispatch();


  const options = [
    { icon: <ListAltIcon />, name: "Orders", func: orders },
    { icon: <PersonIcon />, name: "Profile", func: account },
    {
      icon: (
        <ShoppingCartIcon
          style={{ color: cartItems.length > 0 ? "tomato" : "unset" }}
        />
      ),
      name: `Cart(${cartItems.length})`,
      func: cart,
    },
    { icon: <ExitToAppIcon />, name: "Logout", func: logoutUser },
  ];

  if (user.role === "admin") {
    options.unshift({
      icon: <DashboardIcon />,
      name: "Dashboard",
      func: dashboard,
    });
  }

  function dashboard() {
    history.push("/admin/dashboard");
  }

  function orders() {
    history.push("/orders");
  }
  function account() {
    history.push("/account");
  }
  function cart() {
    history.push("/cart");
  }
  function logoutUser() {
    dispatch(logout());
    alert.success("Logout Successfully");
  }

  return (
<>
    <Backdrop open={open} style={{ zIndex: "10" }} />
      <SpeedDial
        ariaLabel="SpeedDial tooltip example"
        onClose={() => setOpen(false)}
        onOpen={() => setOpen(true)}
        style={{ zIndex: "11" }}
        open={open}
        direction="down"
        className="speedDial"
        icon={
          <img
            className="speedDialIcon"
            src={user.avatar.url ? user.avatar.url : "/Profile.png"}
            alt="Profile"
          />
        }
      >
        {/* {options.map((item) => (
          <SpeedDialAction
            key={item.name}
            icon={item.icon}
            tooltipTitle={item.name}
            onClick={item.func}
            tooltipOpen={window.innerWidth <= 600 ? true : false}
          />
        ))} */}
      </SpeedDial>
  
      <div className="ListAccount">
        <ul id="accountList">
        <li onClick={user.role==="admin"?dashboard:null} id="notmobile" className={user.role==="admin"?"notehide":"hide"}><p><span className="icons"><DashboardIcon/></span><span className="account">Dashboard</span></p></li><hr className={user.role==="admin"?"notehide":"hide"} id="notmobile"/>
        <li onClick={orders}><p><span className="icons"><ListAltIcon/></span><span className="account">Orders</span></p></li><hr/>
        <li onClick={account}><p><span className="icons"><PersonIcon/></span><span className="account">Account</span></p></li><hr/>
        <li onClick={cart}><p><span className="icons"><ShoppingCartIcon/></span><span className="account">Cart</span></p></li><hr/>
        <li onClick={logoutUser}><p><span className="icons"><ExitToAppIcon/></span><span className="account">LogOut</span></p></li><hr/>
      </ul>
      </div>
  </>
  );
};

export default UserOptions;
