// import React from 'react'
import React, { Fragment, useEffect } from "react";
// import { BiNetworkChart, CgMouse } from "react-icons/all";
import "./Home.css";
import ProductCard from "./ProductCard.js";
import MetaData from "../layout/MetaData";
import { clearErrors, getProduct } from "../../actions/productAction";
import { useSelector, useDispatch } from "react-redux";
import Loader from "../layout/Loader/Loader";
import { useAlert } from "react-alert";
import CarouselSlider from "./Slider";
import  ShippingImage from "../../images/shippingimage.png";
import  ProductsImage from "../../images/productsimages.png";

const HomeSlider = () => {
    const alert = useAlert();
    const dispatch = useDispatch();
    const { loading, error, products } = useSelector((state) => state.products);

    useEffect(() => {
        if (error) {
            alert.error(error);
            dispatch(clearErrors());
        }
        dispatch(getProduct());
    }, [dispatch, error, alert]);


    return (
        <Fragment>
            {loading ? (
                <Loader />
            ) : (
                <>
                    <Fragment>
                        <MetaData title="ECOMMERCE" />
                             <CarouselSlider />
                      
                      
                   <div className="shiping-image">
                    <img src={ProductsImage} alt="" />
                   </div>


                        <h2 className="homeHeading">Featured Products</h2>
                        <div className="container" id="container">
                            {products &&
                                products.map((product) => (
                                    <ProductCard key={product._id} product={product} />
                                ))}
                        </div>
                   <div className="shiping-image">
                    <img src={ShippingImage} alt="" />
                   </div>

                   <div className="banner">
                            {/* <p>Welcome to JUSTClick</p> */}
                            <h1>FIND AMAZING PRODUCTS ABOVE</h1>

                            <a href="#container">
                                <button>
                                    Scroll 
                                    {/* <CgMouse /> */}
                                </button>
                            </a>
                        </div>
                        

                        <h2 className="homeHeading">Blogs</h2>
                        
                        
                    </Fragment>
                </>
            )}
        </Fragment>

    )
}

export default HomeSlider

