import React, { Fragment } from "react";
import { Typography, Stepper, StepLabel, Step } from "@material-ui/core";
import LocalShippingIcon from "@material-ui/icons/LocalShipping";
import LibraryAddCheckIcon from "@material-ui/icons/LibraryAddCheck";
import AccountBalanceIcon from "@material-ui/icons/AccountBalance";
import "./CheckoutSteps.css";

const CheckoutSteps = ({ activeStep }) => {
  const steps = [
    {
      label: <Typography className="checkout">Shipping Details</Typography>,
      icon: <LocalShippingIcon className="checkout" />,
    },
    {
      label: <Typography className="checkout">Confirm Order</Typography>,
      icon: <LibraryAddCheckIcon className="checkout" />,
    },
    {
      label: <Typography className="checkout">Payment</Typography>,
      icon: <AccountBalanceIcon className="checkout"/>,
    },
  ];

  const stepStyles = {
    boxSizing: "border-box",
    
  };

  return (
    <Fragment>
      <Stepper alternativeLabel activeStep={activeStep} style={stepStyles}>
        {steps.map((item, index) => (
          <Step
          style={{
            // position: "static",
            // color: activeStep >= index ? "tomato" : "rgba(0, 0, 0, 0.649)",
          }}
            key={index}
            active={activeStep === index ? true : false}
            completed={activeStep >= index ? true : false}
          >
           <StepLabel
              style={{
                
                color: activeStep >= index ? "tomato" : "rgba(0, 0, 0, 0.649)",
              }}
              icon={item.icon}
            >
              {item.label}
            </StepLabel>
          </Step>
        ))}
      </Stepper>
    </Fragment>
  );
};

export default CheckoutSteps;
