import React from "react";


const CarouselSlider = () => {
  return (

<div>
                        <div id="carouselExampleSlidesOnly" class="carousel slide" data-bs-ride="carousel">
                            <div class="carousel-indicators">
                                <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="0" class="active" aria-current="true" aria-label="Slide 1"></button>
                                <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="1" aria-label="Slide 2"></button>
                                <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="2" aria-label="Slide 3"></button>
                            </div>
                            <div class="carousel-inner">
                                
                                <div class="carousel-item active">
                                    <img src=" https://cdn.shopify.com/s/files/1/0057/8938/4802/files/131_desktop-new_1440x.jpg?v=1682347857 " class="d-block w-100" alt="..." />
                                </div>
                                <div class="carousel-item">
                                    <img src="https://cdn.shopify.com/s/files/1/0057/8938/4802/files/new-dimension_desktop-3_1440x.jpg?v=1683280885" class="d-block w-100" alt="..." />
                                </div>
                                <div class="carousel-item">
                                    <img src="https://cdn.shopify.com/s/files/1/0057/8938/4802/files/1550-desktop_9e577988-bcbd-491d-b353-b5d65ee9456b_1440x.jpg?v=1682661291" class="d-block w-100" alt="..." />
                                </div>
                            </div>
                        </div>
</div>
  );
};

export default CarouselSlider;